import React, { useRef, useEffect, useState, useCallback } from 'react';
import * as d3 from 'd3';
import * as topojson from 'topojson-client';
import '../styles/components/MapVisualization.css';

const MapVisualization = ({ 
  ufoData, 
  militaryBaseData, 
  nuclearReactorData, 
  usMapData, 
  viewMode,
  isRendering,
  renderingProgress
}) => {
  const mapRef = useRef(null);
  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });
  const [optimizedData, setOptimizedData] = useState([]);
  
  // Get container dimensions
  useEffect(() => {
    if (!mapRef.current) return;
    
    const updateDimensions = () => {
      const width = mapRef.current.clientWidth;
      const height = Math.max(500, window.innerHeight * 0.6);
      setDimensions({ width, height });
    };
    
    updateDimensions();
    window.addEventListener('resize', updateDimensions);
    
    return () => window.removeEventListener('resize', updateDimensions);
  }, []);
  
  // Process and optimize data for visualization
  useEffect(() => {
    if (!ufoData) return;
    setOptimizedData(ufoData);
  }, [ufoData]);
  
  // Define callback functions for reuse in useEffect
  const addLegend = useCallback((svg) => {
    // Remove any existing legend
    svg.select('.legend').remove();
    
    // Create legend group
    const legend = svg.append('g')
      .attr('class', 'legend')
      .attr('transform', `translate(${dimensions.width - 160}, 20)`);
    
    // Add background
    legend.append('rect')
      .attr('width', 150)
      .attr('height', 120)
      .attr('fill', 'rgba(0, 0, 0, 0.7)')
      .attr('rx', 5);
    
    // Add title
    legend.append('text')
      .attr('x', 75)
      .attr('y', 20)
      .attr('text-anchor', 'middle')
      .attr('fill', '#fff')
      .text('Legend');
    
    // Add UFO sightings
    legend.append('circle')
      .attr('cx', 20)
      .attr('cy', 40)
      .attr('r', 5)
      .attr('fill', 'rgba(100, 255, 218, 0.7)');
    
    legend.append('text')
      .attr('x', 35)
      .attr('y', 45)
      .attr('fill', '#fff')
      .text('UFO Sightings');
    
    // Add military bases
    legend.append('rect')
      .attr('x', 15)
      .attr('y', 60)
      .attr('width', 10)
      .attr('height', 10)
      .attr('fill', 'rgba(220, 20, 60, 0.7)');
    
    legend.append('text')
      .attr('x', 35)
      .attr('y', 70)
      .attr('fill', '#fff')
      .text('Military Bases');
    
    // Add nuclear facilities
    legend.append('polygon')
      .attr('points', '20,100 15,90 25,90')
      .attr('fill', 'rgba(255, 165, 0, 0.7)');
    
    legend.append('text')
      .attr('x', 35)
      .attr('y', 95)
      .attr('fill', '#fff')
      .text('Nuclear Facilities');
  }, [dimensions]);
  
  // Add zoom controls
  const addZoomControls = useCallback((svg, projection, path) => {
    // Remove any existing controls
    svg.select('.zoom-controls').remove();
    
    // Create zoom behavior
    const zoom = d3.zoom()
      .scaleExtent([1, 8])
      .on('zoom', (event) => {
        svg.select('.map-container')
          .attr('transform', event.transform);
      });
    
    // Apply zoom behavior to svg
    svg.call(zoom);
    
    // Create controls group
    const controls = svg.append('g')
      .attr('class', 'zoom-controls')
      .attr('transform', `translate(20, ${dimensions.height - 100})`);
    
    // Add background
    controls.append('rect')
      .attr('width', 40)
      .attr('height', 80)
      .attr('fill', 'rgba(0, 0, 0, 0.7)')
      .attr('rx', 5);
    
    // Add zoom in button
    const zoomIn = controls.append('g')
      .attr('class', 'zoom-button')
      .attr('transform', 'translate(0, 0)')
      .style('cursor', 'pointer');
    
    zoomIn.append('rect')
      .attr('width', 40)
      .attr('height', 40)
      .attr('fill', 'transparent');
    
    zoomIn.append('text')
      .attr('x', 20)
      .attr('y', 25)
      .attr('text-anchor', 'middle')
      .attr('fill', '#fff')
      .style('font-size', '20px')
      .text('+');
    
    zoomIn.on('click', () => {
      svg.transition()
        .duration(300)
        .call(zoom.scaleBy, 1.5);
    });
    
    // Add zoom out button
    const zoomOut = controls.append('g')
      .attr('class', 'zoom-button')
      .attr('transform', 'translate(0, 40)')
      .style('cursor', 'pointer');
    
    zoomOut.append('rect')
      .attr('width', 40)
      .attr('height', 40)
      .attr('fill', 'transparent');
    
    zoomOut.append('text')
      .attr('x', 20)
      .attr('y', 25)
      .attr('text-anchor', 'middle')
      .attr('fill', '#fff')
      .style('font-size', '20px')
      .text('âˆ’');
    
    zoomOut.on('click', () => {
      svg.transition()
        .duration(300)
        .call(zoom.scaleBy, 0.75);
    });
  }, [dimensions]);
  
  // Function to render heatmap
  const renderHeatmap = useCallback((svg, data, projection) => {
    if (!data || data.length === 0) return;
    
    // Create a density scale
    const densityData = d3.contourDensity()
      .x(d => projection([d.longitude, d.latitude])[0])
      .y(d => projection([d.longitude, d.latitude])[1])
      .size([dimensions.width, dimensions.height])
      .bandwidth(20)
      .thresholds(30)
      (data);
    
    // Create a color scale
    const color = d3.scaleSequential(d3.interpolateInferno)
      .domain([0, 0.1]); // Adjust domain based on your data density
    
    // Draw the heatmap
    svg.append('g')
      .attr('class', 'heatmap')
      .selectAll('path')
      .data(densityData)
      .enter()
      .append('path')
      .attr('d', d3.geoPath())
      .attr('fill', d => color(d.value))
      .attr('opacity', 0.7);
  }, [dimensions]);
  
  // Main rendering effect
  useEffect(() => {
    if (!mapRef.current || !dimensions.width || !dimensions.height || !optimizedData || !usMapData) return;
    
    const width = dimensions.width;
    const height = dimensions.height;
    
    const svg = d3.select(mapRef.current).select('svg');
    
    // Clear existing elements
    svg.selectAll('*').remove();
    
    // Set SVG dimensions
    svg
      .attr('width', width)
      .attr('height', height)
      .attr('viewBox', [0, 0, width, height]);
    
    // Create a container for all map elements
    const mapContainer = svg.append('g')
      .attr('class', 'map-container');
    
    // Set up the map projection
    const projection = d3.geoAlbersUsa()
      .translate([width / 2, height / 2])
      .scale(width);
    
    const path = d3.geoPath().projection(projection);
    
    // Draw the US states
    if (usMapData && usMapData.objects && usMapData.objects.states) {
      const states = topojson.feature(usMapData, usMapData.objects.states).features;
      
      mapContainer.append('g')
        .selectAll('path')
        .data(states)
        .enter()
        .append('path')
        .attr('d', path)
        .attr('class', 'state')
        .attr('fill', '#2a2a2a')
        .attr('stroke', '#3a3a3a')
        .attr('stroke-width', 0.5);
    }
    
    // Create a tooltip
    const tooltip = d3.select(mapRef.current)
      .append('div')
      .attr('class', 'tooltip')
      .style('opacity', 0);
    
    // Render based on view mode
    if (viewMode === 'heatmap') {
      renderHeatmap(mapContainer, optimizedData, projection);
    } else {
      // Render UFO sightings as points
      const points = mapContainer.append('g')
        .attr('class', 'points');
      
      points.selectAll('circle')
        .data(optimizedData)
        .enter()
        .append('circle')
        .attr('cx', d => {
          const coords = projection([d.longitude, d.latitude]);
          return coords ? coords[0] : null;
        })
        .attr('cy', d => {
          const coords = projection([d.longitude, d.latitude]);
          return coords ? coords[1] : null;
        })
        .attr('r', viewMode === 'clusters' ? d => Math.sqrt(d.count || 1) * 2 : 3)
        .attr('fill', 'rgba(100, 255, 218, 0.7)')
        .attr('stroke', 'rgba(100, 255, 218, 0.9)')
        .attr('stroke-width', 0.5)
        .attr('class', 'ufo-point')
        .on('mouseover', function(event, d) {
          d3.select(this)
            .attr('r', viewMode === 'clusters' ? Math.sqrt(d.count || 1) * 2.5 : 5)
            .attr('fill', 'rgba(100, 255, 218, 1)');
          
          tooltip.transition()
            .duration(200)
            .style('opacity', 0.9);
          
          const content = viewMode === 'clusters' 
            ? `<strong>${d.count} Sightings</strong><br>Center: ${d.latitude.toFixed(2)}, ${d.longitude.toFixed(2)}`
            : `<strong>${d.city}, ${d.state}</strong><br>${d.date}<br>${d.shape || 'Unknown'} shape<br>${d.duration || 'Unknown'} duration`;
          
          tooltip.html(content)
            .style('left', (event.pageX + 10) + 'px')
            .style('top', (event.pageY - 28) + 'px');
        })
        .on('mouseout', function() {
          d3.select(this)
            .attr('r', viewMode === 'clusters' ? d => Math.sqrt(d.count || 1) * 2 : 3)
            .attr('fill', 'rgba(100, 255, 218, 0.7)');
          
          tooltip.transition()
            .duration(500)
            .style('opacity', 0);
        });
    }
    
    // Render military bases if available
    if (militaryBaseData && militaryBaseData.length > 0) {
      mapContainer.append('g')
        .attr('class', 'military-bases')
        .selectAll('rect')
        .data(militaryBaseData)
        .enter()
        .append('rect')
        .attr('x', d => {
          const coords = projection([d.longitude, d.latitude]);
          return coords ? coords[0] - 4 : null;
        })
        .attr('y', d => {
          const coords = projection([d.longitude, d.latitude]);
          return coords ? coords[1] - 4 : null;
        })
        .attr('width', 8)
        .attr('height', 8)
        .attr('fill', 'rgba(220, 20, 60, 0.7)')
        .attr('stroke', 'rgba(220, 20, 60, 0.9)')
        .attr('stroke-width', 0.5)
        .attr('class', 'military-base')
        .on('mouseover', function(event, d) {
          d3.select(this)
            .attr('width', 10)
            .attr('height', 10)
            .attr('x', function() { return parseFloat(d3.select(this).attr('x')) - 1; })
            .attr('y', function() { return parseFloat(d3.select(this).attr('y')) - 1; })
            .attr('fill', 'rgba(220, 20, 60, 1)');
          
          tooltip.transition()
            .duration(200)
            .style('opacity', 0.9);
          
          tooltip.html(`<strong>${d.name}</strong><br>${d.city}, ${d.state}<br>Type: ${d.type || 'Military Base'}`)
            .style('left', (event.pageX + 10) + 'px')
            .style('top', (event.pageY - 28) + 'px');
        })
        .on('mouseout', function() {
          d3.select(this)
            .attr('width', 8)
            .attr('height', 8)
            .attr('x', function() { return parseFloat(d3.select(this).attr('x')) + 1; })
            .attr('y', function() { return parseFloat(d3.select(this).attr('y')) + 1; })
            .attr('fill', 'rgba(220, 20, 60, 0.7)');
          
          tooltip.transition()
            .duration(500)
            .style('opacity', 0);
        });
    }
    
    // Render nuclear reactors if available
    if (nuclearReactorData && nuclearReactorData.length > 0) {
      mapContainer.append('g')
        .attr('class', 'nuclear-reactors')
        .selectAll('polygon')
        .data(nuclearReactorData)
        .enter()
        .append('polygon')
        .attr('points', d => {
          const coords = projection([d.longitude, d.latitude]);
          if (!coords) return null;
          const x = coords[0];
          const y = coords[1];
          return `${x},${y-6} ${x-5},${y+4} ${x+5},${y+4}`;
        })
        .attr('fill', 'rgba(255, 165, 0, 0.7)')
        .attr('stroke', 'rgba(255, 165, 0, 0.9)')
        .attr('stroke-width', 0.5)
        .attr('class', 'nuclear-reactor')
        .on('mouseover', function(event, d) {
          d3.select(this)
            .attr('transform', 'scale(1.2)')
            .attr('fill', 'rgba(255, 165, 0, 1)');
          
          tooltip.transition()
            .duration(200)
            .style('opacity', 0.9);
          
          tooltip.html(`<strong>${d.name}</strong><br>${d.city}, ${d.state}<br>Type: Nuclear Facility`)
            .style('left', (event.pageX + 10) + 'px')
            .style('top', (event.pageY - 28) + 'px');
        })
        .on('mouseout', function() {
          d3.select(this)
            .attr('transform', 'scale(1)')
            .attr('fill', 'rgba(255, 165, 0, 0.7)');
          
          tooltip.transition()
            .duration(500)
            .style('opacity', 0);
        });
    }
    
    // Add legend and zoom controls
    addLegend(svg);
    addZoomControls(svg, projection, path);
    
  }, [optimizedData, militaryBaseData, nuclearReactorData, usMapData, dimensions, viewMode, addLegend, addZoomControls, renderHeatmap]);
  
  return (
    <div className="map-visualization-container" ref={mapRef}>
      <svg></svg>
      {isRendering && (
        <div className="rendering-overlay">
          <div className="rendering-progress">
            <div className="progress-bar" style={{ width: `${renderingProgress}%` }}></div>
          </div>
          <div className="rendering-text">Rendering visualization... {renderingProgress}%</div>
        </div>
      )}
    </div>
  );
};

export default MapVisualization;