/**
 * Utility functions for optimizing data processing and visualization performance
 */

/**
 * Samples a dataset to reduce its size while maintaining distribution
 * @param {Array} data - The full dataset
 * @param {number} maxPoints - Maximum number of points to include
 * @param {boolean} preserveOutliers - Whether to preserve outliers in the sampling
 * @returns {Array} - Sampled dataset
 */
export const sampleDataset = (data, maxPoints = 2000, preserveOutliers = true) => {
  if (!data || data.length <= maxPoints) return data;
  
  // If we need to reduce the dataset size
  const samplingRatio = maxPoints / data.length;
  
  // For preserving outliers, we'll use a stratified sampling approach
  if (preserveOutliers) {
    // Group data by state to ensure representation across regions
    const dataByState = {};
    data.forEach(item => {
      const state = item.state || 'unknown';
      if (!dataByState[state]) {
        dataByState[state] = [];
      }
      dataByState[state].push(item);
    });
    
    // Sample from each state proportionally
    let sampledData = [];
    Object.entries(dataByState).forEach(([state, stateData]) => {
      const statePointCount = Math.max(1, Math.round(stateData.length * samplingRatio));
      const stateSample = sampleArray(stateData, statePointCount);
      sampledData = [...sampledData, ...stateSample];
    });
    
    return sampledData;
  }
  
  // Simple random sampling if not preserving outliers
  return sampleArray(data, maxPoints);
};

/**
 * Randomly samples an array to the specified size
 * @param {Array} array - Input array
 * @param {number} size - Desired sample size
 * @returns {Array} - Sampled array
 */
const sampleArray = (array, size) => {
  if (array.length <= size) return array;
  
  // Fisher-Yates shuffle and take first 'size' elements
  const shuffled = [...array];
  for (let i = shuffled.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
  }
  
  return shuffled.slice(0, size);
};

/**
 * Clusters points that are close to each other to reduce rendering load
 * @param {Array} data - Array of data points with latitude and longitude
 * @param {number} radius - Clustering radius in degrees
 * @returns {Array} - Clustered data
 */
export const clusterPoints = (data, radius = 0.1) => {
  if (!data || data.length === 0) return [];
  
  const clusters = [];
  const processed = new Set();
  
  // Helper function to calculate distance between two points
  const distance = (p1, p2) => {
    return Math.sqrt(
      Math.pow(p1.latitude - p2.latitude, 2) + 
      Math.pow(p1.longitude - p2.longitude, 2)
    );
  };
  
  // Process each point
  data.forEach((point, index) => {
    if (processed.has(index)) return;
    
    // Start a new cluster
    const cluster = {
      points: [point],
      center: { latitude: point.latitude, longitude: point.longitude },
      count: 1
    };
    
    processed.add(index);
    
    // Find all points within radius of this point
    data.forEach((otherPoint, otherIndex) => {
      if (processed.has(otherIndex) || index === otherIndex) return;
      
      if (distance(point, otherPoint) <= radius) {
        cluster.points.push(otherPoint);
        cluster.count++;
        
        // Update cluster center (average of all points)
        cluster.center.latitude = cluster.points.reduce((sum, p) => sum + p.latitude, 0) / cluster.count;
        cluster.center.longitude = cluster.points.reduce((sum, p) => sum + p.longitude, 0) / cluster.count;
        
        processed.add(otherIndex);
      }
    });
    
    clusters.push(cluster);
  });
  
  return clusters;
};

/**
 * Creates a simplified dataset for initial rendering, with progressive detail loading
 * @param {Array} data - Full dataset
 * @param {number} initialCount - Number of points to include in initial render
 * @returns {Object} - Object containing initial and remaining data
 */
export const createProgressiveDataset = (data, initialCount = 500) => {
  if (!data || data.length <= initialCount) {
    return { initial: data, remaining: [] };
  }
  
  // Sort data by importance (can be customized based on your criteria)
  // Here we're using a simple approach prioritizing more recent sightings
  const sortedData = [...data].sort((a, b) => {
    // Sort by year (descending)
    return (b.year || 0) - (a.year || 0);
  });
  
  return {
    initial: sortedData.slice(0, initialCount),
    remaining: sortedData.slice(initialCount)
  };
};

/**
 * Optimizes data for heatmap rendering
 * @param {Array} data - Array of data points with latitude and longitude
 * @returns {Array} - Optimized data for heatmap
 */
export const optimizeForHeatmap = (data) => {
  if (!data) return [];
  
  // For heatmap, we just need lat, lng and intensity
  return data.map(point => ({
    latitude: point.latitude,
    longitude: point.longitude,
    intensity: 1 // Can be adjusted based on duration, multiple sightings, etc.
  }));
};

/**
 * Calculates viewport-specific data to only render what's visible
 * @param {Array} data - Full dataset
 * @param {Object} bounds - Viewport bounds {north, south, east, west}
 * @returns {Array} - Data points within viewport
 */
export const getVisibleData = (data, bounds) => {
  if (!data || !bounds) return [];
  
  return data.filter(point => {
    return (
      point.latitude <= bounds.north &&
      point.latitude >= bounds.south &&
      point.longitude <= bounds.east &&
      point.longitude >= bounds.west
    );
  });
};
