import React, { useState, useEffect } from 'react';
import MapVisualization from './components/MapVisualization';
import TimelineChart from './components/TimelineChart';
import StateRankingChart from './components/StateRankingChart';
import NuclearProximityAnalysis from './components/NuclearProximityAnalysis';
import ProximityAnalysis from './components/ProximityAnalysis';
import DataControls from './components/DataControls';
import { processUFOData, processMilitaryBaseData, processNuclearReactorData } from './utils/dataProcessing';
import { sampleDataset, clusterPoints } from './utils/dataOptimization';
import './App.css';

function App() {
  const [ufoData, setUfoData] = useState([]);
  const [militaryBaseData, setMilitaryBaseData] = useState([]);
  const [nuclearReactorData, setNuclearReactorData] = useState([]);
  const [usMapData, setUsMapData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [loadingProgress, setLoadingProgress] = useState(0);
  const [dataStats, setDataStats] = useState({
    totalSightings: 0,
    filteredSightings: 0,
    militaryBases: 0,
    nuclearFacilities: 0,
    dateRange: '',
    topState: '',
    topShape: ''
  });
  
  // Filter states
  const [yearRange, setYearRange] = useState([1950, 2023]); // Default range
  const [selectedStates, setSelectedStates] = useState([]);
  const [selectedShapes, setSelectedShapes] = useState([]);
  const [showMilitaryBases, setShowMilitaryBases] = useState(true);
  const [showNuclearReactors, setShowNuclearReactors] = useState(true);
  const [viewMode, setViewMode] = useState('map'); // 'map', 'heatmap', 'clusters'
  const [activeTab, setActiveTab] = useState('overview'); // 'overview', 'analysis', 'data'
  const [dataOptimizationLevel, setDataOptimizationLevel] = useState('medium'); // 'none', 'low', 'medium', 'high'
  const [isRendering, setIsRendering] = useState(false);
  const [renderingProgress, setRenderingProgress] = useState(0);
  
  useEffect(() => {
    const fetchData = async () => {
      try {
        console.log("Starting to fetch data...");
        setLoadingProgress(10);
        
        // Load UFO sightings data
        console.log("Fetching UFO data...");
        const ufoResponse = await fetch(`${process.env.PUBLIC_URL}/data/ufo-sightings.csv`);
        if (!ufoResponse.ok) {
          throw new Error(`Failed to load UFO data: ${ufoResponse.status} ${ufoResponse.statusText}`);
        }
        const ufoText = await ufoResponse.text();
        console.log("UFO data fetched successfully");
        const processedUFOData = processUFOData(ufoText);
        setUfoData(processedUFOData);
        setLoadingProgress(40);
        
        // Load military bases data
        console.log("Fetching military bases data...");
        const basesResponse = await fetch(`${process.env.PUBLIC_URL}/data/military-bases.csv`);
        if (!basesResponse.ok) {
          throw new Error(`Failed to load military bases data: ${basesResponse.status} ${basesResponse.statusText}`);
        }
        const basesText = await basesResponse.text();
        console.log("Military bases data fetched successfully");
        const processedBaseData = processMilitaryBaseData(basesText);
        setMilitaryBaseData(processedBaseData);
        setLoadingProgress(60);
        
        // Load nuclear reactor data
        console.log("Fetching nuclear reactor data...");
        const reactorResponse = await fetch(`${process.env.PUBLIC_URL}/data/nuclear-reactors.csv`);
        if (!reactorResponse.ok) {
          throw new Error(`Failed to load nuclear reactor data: ${reactorResponse.status} ${reactorResponse.statusText}`);
        }
        const reactorText = await reactorResponse.text();
        console.log("Nuclear reactor data fetched successfully");
        const processedReactorData = processNuclearReactorData(reactorText);
        setNuclearReactorData(processedReactorData);
        setLoadingProgress(80);
        
        // Load US map data
        console.log("Fetching US map data...");
        const mapResponse = await fetch(`${process.env.PUBLIC_URL}/data/us-states.json`);
        if (!mapResponse.ok) {
          throw new Error(`Failed to load US map data: ${mapResponse.status} ${mapResponse.statusText}`);
        }
        const mapData = await mapResponse.json();
        setUsMapData(mapData);
        
        console.log("All data loaded successfully!");
        setLoadingProgress(100);
        setTimeout(() => setLoading(false), 500); // Small delay for smooth transition
      } catch (err) {
        console.error("Error loading data:", err);
        setError('Failed to load data: ' + err.message);
        setLoading(false);
      }
    };
    
    fetchData();
  }, []);
  
  // Extract unique UFO shapes for filtering
  const ufoShapes = [...new Set(ufoData.map(d => d.shape))].filter(shape => shape && shape !== 'unknown');
  
  // Extract all states for filtering
  const allStates = [...new Set(ufoData.map(d => d.state))].filter(state => state);
  
  // Filter data based on selected filters
  const filteredUFOData = ufoData.filter(sighting => {
    const yearMatch = sighting.year >= yearRange[0] && sighting.year <= yearRange[1];
    const stateMatch = selectedStates.length === 0 || selectedStates.includes(sighting.state);
    const shapeMatch = selectedShapes.length === 0 || selectedShapes.includes(sighting.shape);
    return yearMatch && stateMatch && shapeMatch;
  });
  
  // State for optimized data
  const [optimizedUFOData, setOptimizedUFOData] = useState([]);
  
  // Apply data optimization when filtered data or optimization settings change
  useEffect(() => {
    const optimizeData = () => {
      setIsRendering(true);
      setRenderingProgress(10);
      
      // Simulate progressive rendering
      const renderingInterval = setInterval(() => {
        setRenderingProgress(prev => {
          if (prev >= 95) {
            clearInterval(renderingInterval);
            return 95;
          }
          return prev + 5;
        });
      }, 100);
      
      let optimizedData = [...filteredUFOData];
      
      try {
        // Apply sampling based on optimization level
        if (dataOptimizationLevel !== 'none') {
          const sampleRatio = 
            dataOptimizationLevel === 'low' ? 0.8 :
            dataOptimizationLevel === 'medium' ? 0.5 :
            dataOptimizationLevel === 'high' ? 0.3 : 1;
          
          optimizedData = sampleDataset(optimizedData, sampleRatio);
        }
        
        // Apply clustering for cluster view mode
        if (viewMode === 'clusters') {
          const clusterRadius = 
            dataOptimizationLevel === 'low' ? 20 :
            dataOptimizationLevel === 'medium' ? 40 :
            dataOptimizationLevel === 'high' ? 60 : 30;
          
          optimizedData = clusterPoints(optimizedData, clusterRadius);
        }
        
        // Finish rendering
        setTimeout(() => {
          clearInterval(renderingInterval);
          setRenderingProgress(100);
          setTimeout(() => {
            setIsRendering(false);
          }, 300);
          
          setOptimizedUFOData(optimizedData);
        }, 500);
      } catch (err) {
        console.error("Error optimizing data:", err);
        clearInterval(renderingInterval);
        setIsRendering(false);
        setOptimizedUFOData(filteredUFOData); // Use original data if optimization fails
      }
    };
    
    optimizeData();
    
    // Cleanup function to clear any intervals if component unmounts during optimization
    return () => {
      setIsRendering(false);
    };
  }, [filteredUFOData, dataOptimizationLevel, viewMode]);
  
  // Update data stats when filtered data changes
  useEffect(() => {
    if (filteredUFOData.length > 0) {
      // Find top state
      const stateCounts = {};
      filteredUFOData.forEach(sighting => {
        if (sighting.state) {
          stateCounts[sighting.state] = (stateCounts[sighting.state] || 0) + 1;
        }
      });
      const topState = Object.entries(stateCounts)
        .sort((a, b) => b[1] - a[1])[0][0];
      
      // Find top shape
      const shapeCounts = {};
      filteredUFOData.forEach(sighting => {
        if (sighting.shape && sighting.shape !== 'unknown') {
          shapeCounts[sighting.shape] = (shapeCounts[sighting.shape] || 0) + 1;
        }
      });
      const topShape = Object.entries(shapeCounts).length > 0 
        ? Object.entries(shapeCounts).sort((a, b) => b[1] - a[1])[0][0]
        : 'N/A';
      
      // Find date range
      const years = filteredUFOData
        .map(d => d.year)
        .filter(year => year);
      const minYear = Math.min(...years);
      const maxYear = Math.max(...years);
      
      setDataStats({
        totalSightings: ufoData.length,
        filteredSightings: filteredUFOData.length,
        militaryBases: militaryBaseData.length,
        nuclearFacilities: nuclearReactorData.length,
        dateRange: `${minYear} - ${maxYear}`,
        topState,
        topShape
      });
    }
  }, [filteredUFOData, ufoData.length, militaryBaseData.length, nuclearReactorData.length]);
  
  if (loading) {
    return (
      <div className="App">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <div className="loading-progress">
            <div 
              className="progress-bar" 
              style={{ width: `${loadingProgress}%` }}
            ></div>
          </div>
          <p className="loading-text">Loading UFO sightings data... {loadingProgress}%</p>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="App">
        <div className="error-container">
          <h2 className="error-title">Error Loading Data</h2>
          <p className="error-message">{error}</p>
          <button className="error-retry" onClick={() => window.location.reload()}>Try Again</button>
        </div>
      </div>
    );
  }
  
  return (
    <div className="App">
      <header className="app-header">
        <h1 className="app-title">UFO Sightings Explorer</h1>
        <p className="app-subtitle">Investigating patterns between UFO sightings, military installations, and nuclear facilities</p>
        
        <nav className="controls-container">
          <button 
            className={activeTab === 'overview' ? 'active' : ''} 
            onClick={() => setActiveTab('overview')}
          >
            Overview
          </button>
          <button 
            className={activeTab === 'analysis' ? 'active' : ''} 
            onClick={() => setActiveTab('analysis')}
          >
            Analysis
          </button>
          <button 
            className={activeTab === 'data' ? 'active' : ''} 
            onClick={() => setActiveTab('data')}
          >
            Data
          </button>
        </nav>
      </header>
      
      <div className="data-stats">
        <div className="stat-card">
          <div className="stat-value">{dataStats.totalSightings.toLocaleString()}</div>
          <div className="stat-label">Total Sightings</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{dataStats.filteredSightings.toLocaleString()}</div>
          <div className="stat-label">Filtered Sightings</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{dataStats.militaryBases.toLocaleString()}</div>
          <div className="stat-label">Military Bases</div>
        </div>
        <div className="stat-card">
          <div className="stat-value">{dataStats.nuclearFacilities.toLocaleString()}</div>
          <div className="stat-label">Nuclear Facilities</div>
        </div>
      </div>
      
      <DataControls 
        yearRange={yearRange}
        onYearRangeChange={setYearRange}
        selectedStates={selectedStates}
        onStateSelection={setSelectedStates}
        allStates={allStates}
        selectedShapes={selectedShapes}
        onShapeSelection={setSelectedShapes}
        ufoShapes={ufoShapes}
        showMilitaryBases={showMilitaryBases}
        onToggleMilitaryBases={() => setShowMilitaryBases(!showMilitaryBases)}
        showNuclearReactors={showNuclearReactors}
        onToggleNuclearReactors={() => setShowNuclearReactors(!showNuclearReactors)}
        viewMode={viewMode}
        onViewModeChange={setViewMode}
        dataOptimizationLevel={dataOptimizationLevel}
        onDataOptimizationChange={setDataOptimizationLevel}
      />
      
      {activeTab === 'overview' && (
        <main>
          <section className="visualization-container">
            <div className="chart-container">
              <div className="chart">
                <MapVisualization 
                  ufoData={optimizedUFOData}
                  militaryBaseData={showMilitaryBases ? militaryBaseData : []}
                  nuclearReactorData={showNuclearReactors ? nuclearReactorData : []}
                  usMapData={usMapData}
                  viewMode={viewMode}
                  isRendering={isRendering}
                  renderingProgress={renderingProgress}
                />
              </div>
              <div className="chart-description">
                <h3>Geographic Distribution</h3>
                <p>
                  This map visualizes the geographic distribution of UFO sightings across the United States, 
                  along with the locations of military bases and nuclear facilities.
                </p>
                <p>
                  The visualization supports multiple view modes:
                </p>
                <ul>
                  <li><strong>Map:</strong> Individual points for each sighting</li>
                  <li><strong>Heatmap:</strong> Density of sightings shown as a heatmap</li>
                  <li><strong>Clusters:</strong> Grouped sightings for better performance</li>
                </ul>
                <p>
                  Data optimization level: <strong>{dataOptimizationLevel}</strong><br />
                  Points displayed: <strong>{optimizedUFOData.length.toLocaleString()}</strong>
                </p>
              </div>
            </div>
          </section>
          
          <section className="visualization-container">
            <div className="chart-container">
              <div className="chart">
                <TimelineChart 
                  ufoData={filteredUFOData} 
                  yearRange={yearRange}
                />
              </div>
              <div className="chart-description">
                <h3>Temporal Distribution</h3>
                <p>
                  This chart shows the number of UFO sightings over time, allowing you to identify 
                  trends, spikes, and patterns in reporting frequency.
                </p>
                <p>
                  Notable periods of increased sightings often correlate with:
                </p>
                <ul>
                  <li>Major media coverage of UFO phenomena</li>
                  <li>Government disclosures or reports</li>
                  <li>Popular culture events (movies, TV shows)</li>
                </ul>
              </div>
            </div>
          </section>
          
          <section className="visualization-container">
            <div className="chart-container">
              <div className="chart">
                <StateRankingChart 
                  ufoData={filteredUFOData}
                  selectedStates={selectedStates}
                />
              </div>
              <div className="chart-description">
                <h3>State Rankings</h3>
                <p>
                  This chart ranks states by the number of reported UFO sightings, 
                  normalized by population to account for differences in state size.
                </p>
                <p>
                  Top state for selected filters: <strong>{dataStats.topState}</strong><br />
                  Most common UFO shape: <strong>{dataStats.topShape}</strong>
                </p>
              </div>
            </div>
          </section>
        </main>
      )}
      
      {activeTab === 'analysis' && (
        <main>
          <section className="visualization-container">
            <div className="chart-container">
              <div className="chart">
                <NuclearProximityAnalysis 
                  ufoData={filteredUFOData}
                  nuclearReactorData={nuclearReactorData}
                />
              </div>
              <div className="chart-description">
                <h3>Nuclear Facility Proximity Analysis</h3>
                <p>
                  This analysis examines the relationship between UFO sightings and their proximity 
                  to nuclear facilities across the United States.
                </p>
                <p>
                  The chart shows the percentage of UFO sightings that occur within different 
                  distance ranges from the nearest nuclear facility.
                </p>
              </div>
            </div>
          </section>
          
          <section className="visualization-container">
            <div className="chart-container">
              <div className="chart">
                <ProximityAnalysis 
                  ufoData={filteredUFOData}
                  militaryBaseData={militaryBaseData}
                  dataType="military"
                />
              </div>
              <div className="chart-description">
                <h3>Military Base Proximity Analysis</h3>
                <p>
                  This analysis examines the relationship between UFO sightings and their proximity 
                  to military installations across the United States.
                </p>
                <p>
                  The chart shows the percentage of UFO sightings that occur within different 
                  distance ranges from the nearest military base.
                </p>
              </div>
            </div>
          </section>
        </main>
      )}
      
      {activeTab === 'data' && (
        <main>
          <section className="visualization-container">
            <div className="chart-container">
              <div className="chart">
                <h3>Data Sources</h3>
                <p>
                  The UFO Sightings Explorer uses data from the following sources:
                </p>
                <ul>
                  <li>UFO Sightings: National UFO Reporting Center (NUFORC)</li>
                  <li>Military Bases: Department of Defense</li>
                  <li>Nuclear Facilities: U.S. Nuclear Regulatory Commission</li>
                </ul>
                <p>
                  Data date range: <strong>{dataStats.dateRange}</strong>
                </p>
              </div>
              <div className="chart-description">
                <h3>Data Processing</h3>
                <p>
                  The data undergoes several processing steps:
                </p>
                <ul>
                  <li>Cleaning and normalization</li>
                  <li>Geocoding (converting addresses to coordinates)</li>
                  <li>Filtering out invalid or incomplete records</li>
                  <li>Optimization for visualization performance</li>
                </ul>
              </div>
            </div>
          </section>
        </main>
      )}
      
      <footer className="footer">
        <p>UFO Sightings Explorer &copy; {new Date().getFullYear()} | Data visualization project</p>
      </footer>
    </div>
  );
}

export default App;