import React, { useRef, useEffect, useState } from 'react';
import * as d3 from 'd3';
import '../styles/components/ProximityAnalysis.css';

// Haversine formula to calculate distance between two points on Earth
const calculateDistance = (lat1, lon1, lat2, lon2) => {
  const R = 6371; // Radius of the Earth in km
  const dLat = (lat2 - lat1) * Math.PI / 180;
  const dLon = (lon2 - lon1) * Math.PI / 180;
  const a = 
    Math.sin(dLat/2) * Math.sin(dLat/2) +
    Math.cos(lat1 * Math.PI / 180) * Math.cos(lat2 * Math.PI / 180) * 
    Math.sin(dLon/2) * Math.sin(dLon/2);
  const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
  return R * c; // Distance in km
};

const ProximityAnalysis = ({ ufoData, facilityData, facilityType }) => {
  const svgRef = useRef();
  const tooltipRef = useRef();
  const [dimensions, setDimensions] = useState({ width: 0, height: 0 });
  
  // Handle window resize
  useEffect(() => {
    const handleResize = () => {
      const containerWidth = svgRef.current.parentElement.clientWidth;
      setDimensions({
        width: containerWidth,
        height: 300, // Fixed height
      });
    };
    
    handleResize();
    window.addEventListener('resize', handleResize);
    
    return () => window.removeEventListener('resize', handleResize);
  }, []);
  
  // Analyze proximity data
  useEffect(() => {
    if (!ufoData.length || !facilityData.length || !dimensions.width || !dimensions.height) return;
    
    const svg = d3.select(svgRef.current);
    const tooltip = d3.select(tooltipRef.current);
    
    // Clear previous rendering
    svg.selectAll('*').remove();
    
    svg
      .attr('width', dimensions.width)
      .attr('height', dimensions.height)
      .attr('viewBox', [0, 0, dimensions.width, dimensions.height])
      .attr('style', 'max-width: 100%; height: auto;');
    
    // Set margins
    const margin = { top: 40, right: 30, bottom: 60, left: 60 };
    const innerWidth = dimensions.width - margin.left - margin.right;
    const innerHeight = dimensions.height - margin.top - margin.bottom;
    
    // Define distance thresholds in km
    const distanceThresholds = [10, 25, 50, 100, 250, 500, 1000];
    
    // Calculate distances for each UFO sighting to the nearest facility
    const proximityData = ufoData.map(sighting => {
      if (!sighting.latitude || !sighting.longitude) return null;
      
      // Find the nearest facility
      let minDistance = Infinity;
      let nearestFacility = null;
      
      facilityData.forEach(facility => {
        if (!facility.latitude || !facility.longitude) return;
        
        const distance = calculateDistance(
          sighting.latitude, sighting.longitude,
          facility.latitude, facility.longitude
        );
        
        if (distance < minDistance) {
          minDistance = distance;
          nearestFacility = facility;
        }
      });
      
      return {
        sighting,
        distance: minDistance,
        facility: nearestFacility
      };
    }).filter(item => item !== null);
    
    // Count sightings within each distance threshold
    const thresholdCounts = distanceThresholds.map(threshold => {
      const count = proximityData.filter(item => item.distance <= threshold).length;
      const percentage = (count / proximityData.length) * 100;
      
      return {
        threshold,
        count,
        percentage
      };
    });
    
    // Create scales
    const xScale = d3.scaleBand()
      .domain(distanceThresholds.map(d => d.toString()))
      .range([0, innerWidth])
      .padding(0.2);
    
    const yScale = d3.scaleLinear()
      .domain([0, 100])
      .range([innerHeight, 0]);
    
    // Create main group
    const g = svg.append('g')
      .attr('transform', `translate(${margin.left}, ${margin.top})`);
    
    // Add title
    svg.append('text')
      .attr('x', dimensions.width / 2)
      .attr('y', 20)
      .attr('text-anchor', 'middle')
      .style('font-size', '16px')
      .style('font-weight', 'bold')
      .text(`UFO Sightings Proximity to ${facilityType === 'military' ? 'Military Bases' : 'Nuclear Facilities'}`);
    
    // Add axes
    const xAxis = d3.axisBottom(xScale)
      .tickFormat(d => `${d} km`);
    
    g.append('g')
      .attr('transform', `translate(0, ${innerHeight})`)
      .call(xAxis)
      .selectAll('text')
      .style('text-anchor', 'end')
      .attr('dx', '-.8em')
      .attr('dy', '.15em')
      .attr('transform', 'rotate(-45)');
    
    const yAxis = d3.axisLeft(yScale)
      .ticks(10)
      .tickFormat(d => `${d}%`);
    
    g.append('g')
      .call(yAxis);
    
    // Add axis labels
    g.append('text')
      .attr('transform', `translate(${innerWidth / 2}, ${innerHeight + 50})`)
      .style('text-anchor', 'middle')
      .text('Distance Threshold');
    
    g.append('text')
      .attr('transform', 'rotate(-90)')
      .attr('y', -40)
      .attr('x', -innerHeight / 2)
      .style('text-anchor', 'middle')
      .text('Percentage of Sightings');
    
    // Add bars
    const barGroups = g.selectAll('.bar-group')
      .data(thresholdCounts)
      .join('g')
      .attr('class', 'bar-group')
      .attr('transform', d => `translate(${xScale(d.threshold.toString())}, 0)`);
    
    const barColor = facilityType === 'military' ? 
      'rgba(220, 20, 60, 0.7)' : // Crimson for military
      'rgba(255, 165, 0, 0.7)';  // Orange for nuclear
    
    barGroups.append('rect')
      .attr('class', 'bar')
      .attr('x', 0)
      .attr('y', d => yScale(d.percentage))
      .attr('width', xScale.bandwidth())
      .attr('height', d => innerHeight - yScale(d.percentage))
      .attr('fill', barColor)
      .attr('stroke', facilityType === 'military' ? 
        'rgba(220, 20, 60, 0.9)' : 
        'rgba(255, 165, 0, 0.9)')
      .attr('stroke-width', 1)
      .on('mouseover', function(event, d) {
        d3.select(this)
          .attr('fill', facilityType === 'military' ? 
            'rgba(220, 20, 60, 0.9)' : 
            'rgba(255, 165, 0, 0.9)');
        
        tooltip
          .style('opacity', 1)
          .html(`
            <strong>Within ${d.threshold} km:</strong><br>
            ${d.count.toLocaleString()} sightings<br>
            ${d.percentage.toFixed(1)}% of total
          `)
          .style('left', (event.pageX + 10) + 'px')
          .style('top', (event.pageY - 28) + 'px');
      })
      .on('mouseout', function() {
        d3.select(this)
          .attr('fill', barColor);
        
        tooltip.style('opacity', 0);
      });
    
    // Add percentage labels on top of bars
    barGroups.append('text')
      .attr('class', 'bar-label')
      .attr('x', xScale.bandwidth() / 2)
      .attr('y', d => yScale(d.percentage) - 5)
      .attr('text-anchor', 'middle')
      .text(d => `${d.percentage.toFixed(1)}%`);
    
    // Add a horizontal reference line at 50%
    g.append('line')
      .attr('x1', 0)
      .attr('x2', innerWidth)
      .attr('y1', yScale(50))
      .attr('y2', yScale(50))
      .attr('stroke', 'rgba(255, 255, 255, 0.2)')
      .attr('stroke-dasharray', '4');
    
    g.append('text')
      .attr('x', innerWidth)
      .attr('y', yScale(50) - 5)
      .attr('text-anchor', 'end')
      .attr('fill', 'rgba(255, 255, 255, 0.5)')
      .style('font-size', '12px')
      .text('50%');
    
  }, [ufoData, facilityData, facilityType, dimensions]);
  
  return (
    <div className="proximity-analysis-container">
      <svg ref={svgRef}></svg>
      <div className="tooltip" ref={tooltipRef}></div>
    </div>
  );
};

export default ProximityAnalysis;